@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700&display=swap);
* {
  margin:0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif ;
}


.container {
  background: linear-gradient(100deg, #1e3c72 50%, #2a5298 0);
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.app-wrapper {
  background-color: hsla(216, 15%, 19%, 0.8);
  width: 40%;
  min-width: 450px;
  height: 80%;
  padding: 30px;
  box-sizing: border-box;
  border-radius: 5px;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.4);
}
.header h1 {
  color: rgb(255, 255, 255);
  font-weight: 400;
  letter-spacing: 2px;
  text-align: center;
  margin: 50px 20px 60px 20px;
  font-size: 40px;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: space-between;
  margin-bottom: 50px;
  width: 100%;
}
.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
}
.list {
  width: 90%;
  margin: auto;
  max-height: 300px;
  overflow: hidden;
  overflow-y: auto;
}

.task-input {
  width: 400px;
  min-width: 250px;
  padding: 15px 10px 10px 20px;
  margin-bottom: 20px;
  margin-right: 10px;
  font-size: 17px;
  color: rgb(255, 255, 255);
  background-color: #1d222b;
  border: 1px solid #fff;
  outline: none;
  border-radius: 20px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
}

.buttons {
  display: flex;
}
.btn {
  margin: 10px;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  background-color: #2a5298;
}

.btn:hover {
  background-color: #fff;
  color: #1e3c72;
}

.add-task-btn {
  min-width: 150px;
  background-color: #2a5298;
  color: #ccc;
  border: none;
  font-size: 14px;
  border-radius: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  outline: none;
  cursor: pointer;
}
.list-item {
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
  padding-bottom: 5px;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  color: #fff;
  transition: all 0.3s ease;
}
.list-item:hover{
  text-decoration: line-through;
}
.list-item span {
  color: #fff;
  font-size: 18px;
}

.task-btn {
  cursor: pointer;
  background-color: transparent;
  border: none;
  outline: none;
}
.btn-delete i {
  font-size: 22px;
  color: #FFF;
  margin-right: 20px;
}

.btn-edit i {
  font-size: 16px;
  color: #bbb;
  margin-right: 20px;
}
.clear-btn {
  width: 150px;
  height: 35px;
  background-color: #2a5298;
  color: #fff;
  border: none;
  font-family: "Montserrat", sans-serif;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  border-radius: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  outline: none;
  cursor: pointer;
}
.no-tasks {
  color: #777;
  text-align: center;
  font-size: 18px;
  margin-top: 20px;
}
